@import url('https://fonts.googleapis.com/css2?family=Cormorant:wght@300&family=Rampart+One&family=Tangerine:wght@400;700&display=swap');

@media only screen and (max-width: 600px) {
  .buttons {
    flex-direction: column;
  }
}

.App {
  text-align: center;
  .App-header {
     min-height: 100vh;
     display: flex;
     flex-direction: column;
     align-items: center;
     justify-content: center;
     font-size: calc(10px + 2vmin);
     color: white;
     #quote-box {
       padding: 20px;
       background-color: rgb(255, 255, 255);
       border-radius: 10px;
       max-width: 500px;
       height: auto;
       letter-spacing: 2px;
       #text{
         font-family: 'Cormorant', serif;
         font-size: 0.9em;
         }
       #author {
        text-align: end;
        font-size: 1.1em;
        font-family: 'Tangerine', cursive;
        font-weight: bold;
      }
      .buttons{
        display: flex;
        justify-content: space-between;
        min-height: 50px;
        #tweet-quote {
          display: flex;
          align-self: center;
          padding: 10px;
          color: white;
          border-radius: 10px;
        }
        #tweet-quote:hover{
          color: #1DA1F2;
        }
        #new-quote {
          padding: 8px;
          border-radius: 10px;
          border: none;
          cursor: pointer ;
          font-size: 0.7em;
          font-weight: 600;
          letter-spacing: 2.2px;
          font-family: 'Cormorant', serif;
        }
      } 
    }
    #da3ker{
      color: white;
      font-size: 0.5em;
      letter-spacing: 2px;
      font-family: 'Rampart One', cursive;
    }
  }
}
