@import url(https://fonts.googleapis.com/css2?family=Cormorant:wght@300&family=Rampart+One&family=Tangerine:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@media only screen and (max-width: 600px){.buttons{flex-direction:column}}.App{text-align:center}.App .App-header{min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App .App-header #quote-box{padding:20px;background-color:#fff;border-radius:10px;max-width:500px;height:auto;letter-spacing:2px}.App .App-header #quote-box #text{font-family:'Cormorant', serif;font-size:0.9em}.App .App-header #quote-box #author{text-align:end;font-size:1.1em;font-family:'Tangerine', cursive;font-weight:bold}.App .App-header #quote-box .buttons{display:flex;justify-content:space-between;min-height:50px}.App .App-header #quote-box .buttons #tweet-quote{display:flex;align-self:center;padding:10px;color:white;border-radius:10px}.App .App-header #quote-box .buttons #tweet-quote:hover{color:#1DA1F2}.App .App-header #quote-box .buttons #new-quote{padding:8px;border-radius:10px;border:none;cursor:pointer;font-size:0.7em;font-weight:600;letter-spacing:2.2px;font-family:'Cormorant', serif}.App .App-header #da3ker{color:white;font-size:0.5em;letter-spacing:2px;font-family:'Rampart One', cursive}

